<template>
  <div id="windowWorkCard">
    <div id="cardInforShower">
      <el-form :model="cardForm" ref="cardForm" :rules="cardFormRules">
        <el-form-item label="ID" prop="cardID">
          <el-input v-model="cardForm.cardID" style="width: 50%;" placeholder="ID()"></el-input>
          <el-button type="primary" @click="searchCard"></el-button>
        </el-form-item>
      </el-form>
      <table border>
        <tr>
          <td>ID{{ this.cardInfor.cardID }}</td>
          <td>{{ this.cardInfor.patientName }}</td>
        </tr>
        <tr>
          <td>{{ this.cardInfor.cardStatus }}</td>
          <td>{{ this.cardInfor.balance }}</td>
        </tr>
      </table>
    </div>
    <div id="cardActionShower">
      <el-button type="primary" @click="createCardDialog = true"></el-button>
        <el-dialog v-model="createCardDialog" title="">
          <el-form :model="createForm" ref="createForm" :rules="createFormRules">
            <el-form-item label="" prop="patientName">
              <el-input v-model="patientName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="idNum">
              <el-input v-model="idNum" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="createCard"></el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
      <el-button type="success" @click="replaceCardDialog = true"></el-button>
        <el-dialog v-model="replaceCardDialog" title="">
          <el-form :model="createForm" ref="createForm" :rules="createFormRules">
            <el-form-item label="" prop="patientName">
              <el-input v-model="patientName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="idNum">
              <el-input v-model="idNum" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="success" @click="replaceCard"></el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
      <el-button type="warning" @click="chargeCardDialog = true"></el-button>
        <el-dialog v-model="chargeCardDialog" title="" @closed="chargeCardDialogClosed">
          <el-form :model="chargeForm" ref="chargeForm" :rules="chargeFormRules">
            <el-form-item label="ID" prop="cardID">
              <el-input v-model="cardForm.cardID" placeholder="ID"></el-input>
            </el-form-item>
            <el-form-item label="" prop="money">
              <el-slider v-model="chargeForm.money" placeholder="" :min="0" :max="5000" :step="0.01" show-input></el-slider>
            </el-form-item>
            <el-form-item>
              <el-button type="warning" @click="chargeCard"></el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
      <el-button type="danger" @click="destroyCardDialog = true"></el-button>
        <el-dialog v-model="destroyCardDialog" title="" @closed="destroyCardDialog">
          <p>cardID{{ cardForm.cardID }} </p>
          <p style="margin-bottom: 1rem;"></p>
          <el-button type="danger" @click="destroyCard"></el-button>
          <el-button @click="destroyCardDialog = false"></el-button>
        </el-dialog>
    </div>
  </div>
</template>

<style>
#windowWorkCard{
  display: flex;
  justify-content: space-around;
}
#cardInforShower{
  width: 75%;
  margin-top: 3%;
}
#cardInforShower .el-button{
  margin-left: 2rem;
}
#cardInforShower table{
  margin-top: 5%;
  margin-left: 5%;
  width: 60%;
  height: 50%;
  font-size: 1.2rem;
  border-collapse: collapse;
  text-align: center;
}
#cardActionShower{
  width: 10%;
  height: 70vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
#cardActionShower > .el-button{
  font-size: 1.5rem;
  border-radius: 2rem;
  height: 10%;
}
</style>

<script>
import { mapMutations, mapState } from 'vuex'
import axios from 'axios'
import { ElMessage } from 'element-plus'
// .catch((result) => { this.$store.state.errorReport(result) })

export default {
  name: 'cardView',
  data () {
    return {
      cardForm: {
        cardID: ''
      },
      cardFormRules: {
        cardID: [
          { required: true, message: 'ID', trigger: 'blur' }
        ]
      },
      cardInfor: {
        cardID: '',
        patientName: '',
        cardStatus: '',
        balance: ''
      },
      createCardDialog: false,
      replaceCardDialog: false,
      chargeCardDialog: false,
      destroyCardDialog: false,
      createForm: {
        patientName: '',
        idNum: ''
      },
      createFormRules: {
        patientName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        idNum: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      chargeForm: {
        cardID: '',
        money: ''
      },
      chargeFormRules: {
        cardID: [
          { required: true, message: '', trigger: 'blur' }
        ],
        money: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      cardInforAPI: '/window-work/card/cardInfor',
      createCardAPI: '/window-work/card/createCard',
      replaceCardAPI: '/window-work/card/replaceCard',
      chargeCardAPI: '/window-work/card/chargeCard',
      destroyCardAPI: '/window-work/card/destroyCard'
    }
  },
  computed: {
    ...mapState('windowWork', ['patientName2', 'idNum2']),
    patientName: {
      get () {
        return this.patientName2
      },
      set (newValue) {
        this.setPatientName(newValue)
      }
    },
    idNum: {
      get () {
        return this.idNum2
      },
      set (newValue) {
        this.setIdNum(newValue)
      }
    }
  },
  methods: {
    ...mapMutations('windowWork', ['setPatientName', 'setIdNum']),
    searchCard () {
      this.$refs.cardForm.validate()
        .then(() => {
          axios.get(this.cardInforAPI, {
            params: {
              token: localStorage.getItem('token'),
              cardID: this.cardForm.cardID
            }
          }).then((result) => {
            this.cardInfor.cardID = this.cardForm.cardID
            this.cardInfor.patientName = result.data.patientName
            this.cardInfor.cardStatus = result.data.cardStatus
            this.cardInfor.balance = result.data.balance
          }).catch((result) => { this.$store.state.errorReport(result) })
        }).catch(() => {})
    },
    createCard () {
      this.createForm.patientName = this.patientName
      this.createForm.idNum = this.idNum
      this.$refs.createForm.validate()
        .then(() => {
          axios.post(this.createCardAPI, {
            patientName: this.createForm.patientName,
            idNum: this.createForm.idNum
          }).then((result) => {
            alert(`ID${result.data.cardID}`)
            this.createCardDialog = false
          }).catch((result) => { this.$store.state.errorReport(result) })
        })
        .catch(() => {})
    },
    replaceCard () {
      this.createForm.patientName = this.patientName
      this.createForm.idNum = this.idNum
      this.$refs.createForm.validate()
        .then(() => {
          axios.post(this.replaceCardAPI, {
            patientName: this.createForm.patientName,
            idNum: this.createForm.idNum
          }).then((result) => {
            alert(`ID${result.data.cardID}`)
            this.replaceCardDialog = false
          }).catch((result) => { this.$store.state.errorReport(result) })
        })
        .catch(() => {})
    },
    chargeCard () {
      this.chargeForm.cardID = this.cardForm.cardID
      this.$refs.chargeForm.validate()
        .then(() => {
          axios.post(this.chargeCardAPI, {
            cardID: this.chargeForm.cardID,
            money: this.chargeForm.money
          }).then((result) => {
            ElMessage({
              type: 'success',
              message: ''
            })
            this.chargeCardDialog = false
            this.searchCard()
          }).catch((result) => { this.$store.state.errorReport(result) })
        })
        .catch(() => {})
    },
    destroyCard () {
      axios.post(this.destroyCardAPI, {
        cardID: this.cardForm.cardID
      }).then(() => {
        ElMessage({
          type: 'success',
          message: ''
        })
        this.destroyCardDialog = false
      }).catch((result) => { this.$store.state.errorReport(result) })
    }
  }
}
</script>
