<template>
  <el-tabs id="docWorkInforConsent">
    <el-tab-pane label="">
      <el-button type="success" @click="printNotice('operationNotice')"></el-button>
        <div class="notice">
          <h1></h1>
          <p class="text">________________________________</p>
          <p class="text"></p>
          <p class="text">________________//</p>
          <p class="text"></p>
          <p class="date">{{ `${new Date().toLocaleDateString()}  ${new Date().toLocaleTimeString()}` }}</p>
          <div class="sign">
            <p class="patientSign"></p>
            <p class="doctorSign"></p>
          </div>
        </div>
    </el-tab-pane>
    <el-tab-pane label="">
      <el-button type="success" @click="printNotice('drugNotice')"></el-button>
      <div class="notice">
        <h1></h1>
        <p class="text">________________________________</p>
        <p class="text"></p>
        <p class="text">/</p>
        <p class="text"></p>
        <p class="date">{{ `${new Date().toLocaleDateString()}  ${new Date().toLocaleTimeString()}` }}</p>
        <div class="sign">
          <p class="patientSign"></p>
          <p class="doctorSign"></p>
        </div>
      </div>
    </el-tab-pane>
  </el-tabs>
</template>

<style>
#docWorkInforConsent .notice{
  margin: 2rem 10%;
  width: 80%;
  display: flex;
  flex-direction: column;
}
#docWorkInforConsent h1{
  align-self: center;
  margin: 1rem 0;
}
#docWorkInforConsent .text{
  margin-left: 3%;
  margin: 1rem 3%;
  text-indent:2em;
}
#docWorkInforConsent .date{
  margin-top: 1rem;
  margin-left: 3%;
}
#docWorkInforConsent .sign{
  margin: 1rem 0;
  margin-left: 5%;
  width: 30%;
  display: flex;
  justify-content: space-between;
}
</style>

<script>
import { mapMutations, mapState } from 'vuex'
// import axios from 'axios'
// .catch((result) => { this.$store.state.errorReport(result) })

export default {
  name: 'inforConsentView',
  data () {
    return {}
  },
  computed: {
    ...mapState('docWork', ['patientId2', 'recordId2']),
    patientId: {
      get () {
        return this.patientId2
      },
      set (newValue) {
        this.setPatientId(newValue)
      }
    },
    recordId: {
      get () {
        return this.recordId2
      },
      set (newValue) {
        this.setRecordId(newValue)
      }
    }
  },
  methods: {
    ...mapMutations('docWork', ['setPatientId', 'setRecordId']),
    printNotice (routerName) {
      window.open(this.$router.resolve({ name: routerName }).href, '_blank')
    }
  },
  async beforeMount () {}
}
</script>
