<template>
  <div id="basic-main">
    <el-page-header :icon="null" id="basic-header">
      <template #title>
        <el-radio-group v-model="menuCollapseFlagTransfer">
          <el-radio-button :label="true"></el-radio-button>
          <el-radio-button :label="false"></el-radio-button>
        </el-radio-group>
      </template>
      <template #content>
        <div id="userProfilePhoto"><!----></div>
        <p class="userName">{{ userName }}</p>
        <el-tag :type="userRoleTagType" effect="light">{{ userRole }}</el-tag>
        <p class="welcomeLogo">HIS</p>
      </template>
      <template #extra>
        <div id="author-address">
          <img src="../assets/images/github.webp" alt="GitHub">
          <div>
            <p><a href="https://github.com/LoveEmiliaForever" target="_blank">LoveEmiliaForever</a></p>
            <p><a href="www.baidu.com" target="_blank">www.baidu.com</a></p>
          </div>
        </div>
        <el-button type="danger" plain @click="onLogOut">HIS</el-button>
      </template>
    </el-page-header>
    <div id="basic-body">
      <el-menu mode="vertical" :collapse="menuCollapseFlagTransfer" :default-active="this.$router.currentRoute._value.path" router>
        <el-menu-item v-for="(item, index) in Object.entries(permissions)" :index="item[1]" :key="index">
          <el-icon v-if="item[0] == ''"><House /></el-icon>
          <el-icon v-else-if="item[0] == ''"><User /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Avatar /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Collection /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Box /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Money /></el-icon>
          <el-icon v-else-if="item[0] == ''"><ChatLineRound /></el-icon>
          <el-icon v-else-if="item[0] == ''"><ScaleToOriginal /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Wallet /></el-icon>
          <el-icon v-else-if="item[0] == ''"><ReadingLamp /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Sugar /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Scissor /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Help /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Goods /></el-icon>
          <el-icon v-else-if="item[0] == ''"><SoldOut /></el-icon>
          <el-icon v-else-if="item[0] == ''"><TakeawayBox /></el-icon>
          <el-icon v-else-if="item[0] == ''"><TakeawayBox /></el-icon>
          <el-icon v-else-if="item[0] == ''"><TakeawayBox /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Tickets /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Tickets /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Tickets /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Grid /></el-icon>
          <el-icon v-else-if="item[0] == ''"><DataBoard /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Platform /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Postcard /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Grid /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Menu /></el-icon>
          <el-icon v-else-if="item[0] == ''"><User /></el-icon>
          <el-icon v-else-if="item[0] == ''"><HomeFilled /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Place /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Files /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Reading /></el-icon>
          <el-icon v-else-if="item[0] == ''"><User /></el-icon>
          <el-icon v-else-if="item[0] == ''"><Histogram /></el-icon>
          <el-icon v-else><House /></el-icon>
          <template #title>{{ item[0] }}</template>
        </el-menu-item>
      </el-menu>
      <slot></slot>
    </div>
  </div>

</template>

<style>
#basic-main{
  display: flex;
  flex-direction: column;
  height: 100vh;
}
#basic-header{
  padding: 1%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.041);
}
#basic-header .el-page-header__left{
  width: 50%;
  display: flex;
  justify-content: space-between;
}
#basic-header .el-page-header__content{
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
#userProfilePhoto{
  background-image: url(../assets/images/.webp);
  background-size: cover;
  width: 5vw;
  height: 5vw;
  border-radius: 5vw;
  margin-right: 1rem;
}
#basic-header .userName{
  font-size: 2rem;
  font-weight: 700;
  text-shadow: 0 1px 1px 1px rgba(0, 0, 0, 0.322);
  margin-right: 0.5rem;
}
#basic-header .welcomeLogo{
  font-size: 1.5rem;
  font-weight: 700;
  color: rgba(0, 0, 0, 0.473);
  height: 2rem;
  padding-top: 1.0rem;
  margin-left: 2rem;
}
#basic-header .el-page-header__extra{
  width: 40%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
#basic-header #author-address{
  width: 45%;
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
  border-radius: 0.5rem;
  border: 1px solid rgba(0, 0, 0, 0.158);
}
#basic-header #author-address img{
  width: 20%;
}
#basic-header #author-address a:link,#basic-header #author-address a:visited{
  color: rgba(0, 140, 255, 0.808);
  text-decoration: none;
}
#basic-header #author-address a:hover{
  color: rgba(0, 140, 255, 0.418);
  text-decoration: underline;
}
#basic-header #author-address > div{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
#basic-header #author-address p:nth-of-type(1){
  font-size: 1.1rem;
}
#basic-body{
  display: flex;
  flex: 1;
  height: 10%;
}
#basic-body .el-menu{
  overflow: scroll;
  /*  IEEdge  Firefox  */
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
/*  ChromeSafari  Opera  */
#basic-body .el-menu::-webkit-scrollbar{
  display: none;
}
.contents{
  flex: 1;
  overflow: scroll;
}
</style>

<script>
import { ElNotification } from 'element-plus'
import { mapState } from 'vuex'
export default {
  data () {
    return {
      collapseFlag: false
    }
  },
  computed: {
    ...mapState(['permissions', 'userName', 'userRole']),
    menuCollapseFlagTransfer: {
      get () {
        return this.$store.state.menuCollapseFlag
      },
      set (newValue) {
        this.$store.commit({ type: 'setMenuCollapseFlag', newValue: newValue })
      }
    },
    userRoleTagType: {
      get () {
        switch (this.userRole) {
          case '':
            return ''
          case '':
            return 'success'
          case '':
            return 'danger'
          default:
            return 'warning'
        }
      }
    }
  },
  methods: {
    onLogOut () {
      localStorage.removeItem('token')
      ElNotification({
        title: '',
        message: '',
        position: 'bottom-right',
        type: 'success'
      })
      this.$router.push({ name: 'login' })
    }
  },
  mounted () {
    if (this.$store.state.permissions === '') {
      this.$store.commit('getPermissions', this)
    }
    if (this.$store.state.userName === '' || this.$store.state.userRole === '') {
      this.$store.commit('getUserData', this)
    }
  }
}
</script>
