<template>
  <div class="contents" id="drugWarehouseWorkDrugManage">
    <div>
      <el-button type="primary" @click="wareDialog = true"></el-button>
      <el-button type="warning" @click="exWareDialog = true"></el-button>
      <el-button type="danger" @click="lossesDialog = true"></el-button>
    </div>
    <el-table v-if="drugInfor" :data="drugInfor" border>
      <el-table-column prop="drugName" label=""></el-table-column>
      <el-table-column prop="drugNum" label=""></el-table-column>
      <el-table-column prop="drugType" label=""></el-table-column>
      <el-table-column prop="appearance" label=""></el-table-column>
      <el-table-column prop="prescriptionStatus" label=""></el-table-column>
      <el-table-column prop="manufactureDate" label=""></el-table-column>
      <el-table-column prop="expirationDate" label=""></el-table-column>
      <el-table-column prop="warehousingDate" label=""></el-table-column>
      <el-table-column prop="ExWarehousingDate" label=""></el-table-column>
      <el-table-column prop="drugPrice" label=""></el-table-column>
    </el-table>
    <el-dialog title="" @close="wareDialog = false; this.$refs.warehousingForm.resetFields()" v-model="wareDialog">
      <el-form :model="warehousingForm" ref="warehousingForm" :rules="warehousingRules">
        <el-form-item label="" prop="drugName">
          <el-input v-model="warehousingForm.drugName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="drugNum">
          <el-slider v-model="warehousingForm.drugNum" :min="1" show-input></el-slider>
        </el-form-item>
        <el-form-item label="" prop="drugType">
          <el-select v-model="warehousingForm.drugType" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="appearance">
          <el-select v-model="warehousingForm.appearance" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="prescriptionStatus">
          <el-select v-model="warehousingForm.prescriptionStatus" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="manufactureDate">
          <el-date-picker v-model="warehousingForm.manufactureDate" type="date" placeholder=""></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="expirationDate">
          <el-date-picker v-model="warehousingForm.expirationDate" type="date" placeholder=""></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="warehousing"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-dialog title="" @close="exWareDialog = false; this.$refs.exWarehousingForm.resetFields()" v-model="exWareDialog">
      <el-form :model="exWarehousingForm" ref="exWarehousingForm" :rules="exWarehousingRules">
        <el-form-item label="" prop="drugName">
          <el-input v-model="exWarehousingForm.drugName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="drugNum">
          <el-slider v-model="exWarehousingForm.drugNum" :min="1" show-input></el-slider>
        </el-form-item>
        <el-form-item label="" prop="reason">
          <el-input v-model="exWarehousingForm.reason"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="exWarehousing"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-dialog title="" @close="lossesDialog = false; this.$refs.lossesForm.resetFields()" v-model="lossesDialog">
      <el-form :model="lossesForm" ref="lossesForm" :rules="lossesRules">
        <el-form-item label="" prop="drugName">
          <el-input v-model="lossesForm.drugName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="drugNum">
          <el-slider v-model="lossesForm.drugNum" :min="1" show-input></el-slider>
        </el-form-item>
        <el-form-item label="" prop="reason">
          <el-input v-model="lossesForm.reason"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="losses"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<style>
#drugWarehouseWorkDrugManage{
  padding: 1rem;
}
#drugWarehouseWorkDrugManage button{
  margin-bottom: 1rem;
}
</style>

<script>
import axios from 'axios'
import { ElMessage } from 'element-plus'
// .catch((result) => { this.$store.state.errorReport(result) })

export default {
  name: 'workTimeView',
  data () {
    return {
      drugInfor: false,
      drugInforAPI: '/drug-warehouse-work/drugManage/drugInfor',
      wareDialog: false,
      warehousingForm: {
        drugName: '',
        drugNum: '',
        drugType: '',
        appearance: '',
        prescriptionStatus: '',
        manufactureDate: '',
        expirationDate: ''
      },
      warehousingRules: {
        drugName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        drugNum: [
          { required: true, message: '', trigger: 'blur' }
        ],
        drugType: [
          { required: true, message: '', trigger: 'blur' }
        ],
        appearance: [
          { required: true, message: '', trigger: 'blur' }
        ],
        prescriptionStatus: [
          { required: true, message: '', trigger: 'blur' }
        ],
        manufactureDate: [
          { required: true, message: '', trigger: 'blur' }
        ],
        expirationDate: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      warehousingAPI: '/drug-warehouse-work/drugManage/warehousing',
      exWareDialog: false,
      exWarehousingForm: {
        drugName: '',
        drugNum: '',
        reason: ''
      },
      exWarehousingRules: {
        drugName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        drugNum: [
          { required: true, message: '', trigger: 'blur' }
        ],
        reason: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      exWarehousingAPI: '/drug-warehouse-work/drugManage/exWarehousing',
      lossesDialog: false,
      lossesForm: {
        drugName: '',
        drugNum: '',
        reason: ''
      },
      lossesRules: {
        drugName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        drugNum: [
          { required: true, message: '', trigger: 'blur' }
        ],
        reason: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      lossesAPI: '/drug-warehouse-work/drugManage/losses'
    }
  },
  methods: {
    async getDrugInfor () {
      await axios.get(this.drugInforAPI, {
        params: {
          token: localStorage.getItem('token')
        }
      }).then((result) => {
        this.drugInfor = result.data
      }).catch((result) => { this.$store.state.errorReport(result) })
    },
    warehousing () {
      this.$refs.warehousingForm.validate()
        .then(() => {
          axios.post(this.warehousingAPI, {
            token: localStorage.getItem('token'),
            warehousingDate: new Date(),
            drugName: this.warehousingForm.drugName,
            drugNum: this.warehousingForm.drugNum,
            drugType: this.warehousingForm.drugType,
            appearance: this.warehousingForm.appearance,
            prescriptionStatus: this.warehousingForm.prescriptionStatus,
            manufactureDate: this.warehousingForm.manufactureDate,
            expirationDate: this.warehousingForm.expirationDate
          }).then(() => {
            ElMessage({
              type: 'success',
              message: ''
            })
            this.$refs.warehousingForm.resetFields()
            this.wareDialog = false
          }).catch((result) => { this.$store.state.errorReport(result) })
        })
        .catch(() => {})
    },
    exWarehousing () {
      this.$refs.exWarehousingForm.validate()
        .then(() => {
          axios.post(this.exWarehousingAPI, {
            token: localStorage.getItem('token'),
            exWarehousingDate: new Date(),
            drugName: this.exWarehousingForm.drugName,
            drugNum: this.exWarehousingForm.drugNum,
            reason: this.exWarehousingForm.reason
          }).then(() => {
            ElMessage({
              type: 'success',
              message: ''
            })
            this.$refs.exWarehousingForm.resetFields()
            this.exWareDialog = false
          }).catch((result) => { this.$store.state.errorReport(result) })
        })
        .catch(() => {})
    },
    losses () {
      this.$refs.lossesForm.validate()
        .then(() => {
          axios.post(this.lossesAPI, {
            token: localStorage.getItem('token'),
            lossesDate: new Date(),
            drugName: this.lossesForm.drugName,
            drugNum: this.lossesForm.drugNum,
            reason: this.lossesForm.reason
          }).then(() => {
            ElMessage({
              type: 'success',
              message: ''
            })
            this.$refs.lossesForm.resetFields()
            this.lossesDialog = false
          }).catch((result) => { this.$store.state.errorReport(result) })
        })
        .catch(() => {})
    }
  },
  async beforeMount () {
    await this.getDrugInfor()
  }
}
</script>
